.subtitle {
  text-transform: uppercase;
  font-family: $stapel;
  font-size: 32px;
  line-height: 38px;
  @include screen(laptop) {
    font-size: 94px;
    line-height: 112px;
  }
  @include screen(desktop) {
    font-size: 120px;
    line-height: 144px;
  }
  &-white {
    color: $white-bright;
  }
  &-sm {
    font-size: 26px;
    line-height: 31px;
    @include screen(laptop) {
      font-size: 60px;
      line-height: 72px;
    }
    @include screen(desktop) {
      font-size: 80px;
      line-height: 96px;
    }
  }
}