.slider {
  background-color: $blue-light;
  overflow: hidden;
  &-blue {
    background-color: $blue-primary;
    .headline {
      .swiper-button-prev,
      .swiper-button-next {
        border-color: $white-primary;
        svg {
          path {
            fill: $white-primary;
          }
        }
      }
    }
  }
  .swiper-slider-two {
    .swiper {
      margin-top: 25px;
      @include screen(laptop) {
        margin-top: 40px;
      }
      &-wrapper {
        align-items: stretch;
      }
      &-slide {
        padding: 20px 25px;
        height: auto;
        display: flex;
        align-items: flex-start;
        flex-direction: column;
        justify-content: space-between;
        border: 1px solid #BDC8D0;
        transition: border-color $transition-default;
        &:hover {
          border-color: #a0a0a0;
        }
        @include screen(laptop) {
          padding: 45px;
        }
        @include screen(desktop) {
          padding: 60px;
        }
      }
    }
  }
  .swiper-slider-three {
    .headline {
      .subtitle {
        br {
          @include screen(laptop) {
            display: none;
          }
        }
      }
    }
    .swiper {
      overflow: visible;
      margin-top: 25px;
      @include screen(laptop) {
        margin-top: 57px;
      }
      &-wrapper {
        align-items: stretch;
      }
      &-slide {
        height: auto;
        padding: 25px 30px;
        background-color: #3174AA;
        transition: background-color $transition-default;
        @include screen(laptop) {
          padding: 35px 50px;
          &:hover {
            background-color: #4682B2;
          }
        }
        @include screen(desktop) {
          padding: 40px 55px;
        }
        .slider__header {
          h3 {
            margin-bottom: 10px;
            padding-bottom: 65px;
            color: $white-bright;
            font-size: 20px;
            line-height: 24px;
            @include screen(laptop) {
              padding-bottom: 70px;
              margin-bottom: 20px;
              font-size: 32px;
              line-height: 38px;
            }
            @include screen(desktop) {
              font-size: 45px;
              line-height: 54px;
            }
          }
        }
        .slider__body {
          margin: 0;
          grid-template-columns: repeat(1, minmax(0, 1fr));
          p {
            color: $white-bright;
          }
        }
      }
    }
  }
  &__header {
    h3 {
      font-family: $stapel;
      color: $black-dark;
      padding-bottom: 15px;
      margin-bottom: 15px;
      font-size: 20px;
      line-height: 24px;
      position: relative;
      &:before {
        content: '';
        position: absolute;
        left: 0;
        height: 1px;
        bottom: 0;
        transition: width $transition-default;
        width: 0;
        background-color: #BDC8D0;
      }
      &.active {
        &::before {
          width: 100%;
        }
      }
      @include screen(laptop) {
        margin-bottom: 25px;
        padding-bottom: 20px;
        font-size: 32px;
        line-height: 38px;
      }
      @include screen(desktop) {
        font-size: 45px;
        line-height: 54px;
      }
    }
    p {
      color: $black-primary;
      font-size: 14px;
      line-height: 17.5px;
      @include screen(laptop) {
        font-size: 16px;
        line-height: 20px;
      }
      @include screen(desktop) {
        font-size: 20px;
        line-height: 25px;
      }
    }
  }
  &__body {
    display: grid;
    margin-top: 84px;
    align-items: flex-end;
    grid-template-columns: repeat(1, minmax(0, 1fr));
    @include screen(laptop) {
      grid-template-columns: repeat(2, minmax(0, 1fr));
    }
    @include screen(desktop) {
      margin-top: 160px;
    }
    &__date {
      p {
        color: $black-primary;
        font-size: 14px;
        line-height: 14px;
        @include screen(desktop) {
          font-size: 16px;
          line-height: 21px;
        }
      }
      span {
        text-transform: uppercase;
        font-weight: 700;
        font-size: 14px;
        line-height: 14px;
        display: inline-block;
        margin-top: 15px;
        color: $black-primary;
        @include screen(laptop) {
          margin-top: 20px;
        }
        @include screen(desktop) {
          line-height: 21px;
          font-size: 16px;
        }
      }
    }
    &__content {
      margin-top: 25px;
      @include screen(laptop) {
        margin-top: 0;
      }
      p {
        font-size: 14px;
        line-height: 17.5px;
        color: $black-primary;
        @include screen(laptop) {
          -webkit-line-clamp: 5;
          display: -webkit-box;
          -webkit-box-orient: vertical;  
          overflow: hidden;
          font-size: 16px;
          line-height: 20px;
        }
        @include screen(desktop) {
          -webkit-line-clamp: 4;
          font-size: 20px;
          line-height: 25px;
        }
      }
    }
  }
}