.about-page {
  &__parallax {
    overflow: hidden;
    height: 180px;
    position: relative;
    @include screen(laptop) {
      height: 312px;
    }
    &:before {
      content: '';
      position: absolute;
      height: 100%;
      inset: 0;
      width: 100%;
      background-color: #00417B;
      opacity: 0.2;
      z-index: 2;
    }
    img {
      position: absolute;
      object-fit: cover;
      width: 100%;
      @include screen(laptop) {
        top: -300px;
      }
      @include screen(desktop) {
        top: -500px;
      }
    }
  }
  &__slogan {
    font-family: $stapel;
    font-size: 18px;
    line-height: 21px;
    color: $black-primary;
    margin-top: 60px;
    display: grid;
    gap: 60px;
    @include screen(laptop) {
      grid-template-columns: repeat(2, minmax(0, 1fr));
      font-size: 24px;
      line-height: 30px;
    }
    @include screen(desktop) {
      font-size: 34px;
      line-height: 43px;
    }
    p {
      grid-column-start: 1;
      @include screen(laptop) {
        grid-column-start: 2;
      }
    }
  }
  .about__items {
    margin-top: 35px;
    padding-bottom: 60px;
    @include screen(laptop) {
      padding-bottom: 120px;
      margin-top: 60px;
    }
  }
}