.contacts {
  &__buttons {
    justify-content: space-between;
    flex-direction: column;
    align-items: flex-start;
    padding: 23px 15px;
    display: none;
    gap: 25px;
    box-shadow: 0px 0px 5px 0px #0C28401F;
    &.active {
      display: flex;
    }
    @include screen(laptop) {
      display: flex;
      flex-direction: row;
      padding: 0;
      margin-top: 60px;
      box-shadow: none;
      border-bottom: 1px solid #BFC3C7;
      align-items: center;
      margin-top: 70px;
    }
    @include screen(desktop) {
      margin-top: 80px;
    }
  }
  &__current {
    margin-top: 60px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid #3B4A58;
    padding-bottom: 15px;
    span {
      font-family: $stapel;
      font-size: 20px;
      line-height: 24px;
    }
    @include screen(laptop) {
      display: none;
    }
    svg {
      transition: transform $transition-default;
      path {
        transition: stroke $transition-default;
      }
    }
    &.active {
      svg {
        transform: rotate(180deg);
        path {
          stroke: $black-dark;
        }
      }
    }
  }
  &__button {
    font-family: $stapel;
    color: #B4B9BE;
    text-transform: uppercase;
    position: relative;
    transition: color $transition-default;
    &:before {
      content: '';
      position: absolute;
      bottom: -1px;
      width: 100%;
      opacity: 0;
      transition: opacity $transition-default;
      left: 0;
      background-color: $black-dark;
      height: 1px;
    }
    @include screen(laptop) {
      font-size: 28px;
      padding: 15px 0;
      line-height: 33px;
    }
    @include screen(desktop) {
      font-size: 36px;
      line-height: 43px;
    }
    &.active {
      color: $black-dark;
      &:before {
        @include screen(laptop) {
          opacity: 1;
        }
      }
    }
  }
  &__blocks {
    margin-top: 45px;
    @include screen(laptop) {
      margin-top: 70px;
    }
    @include screen(desktop) {
      margin-top: 80px;
    }
  }
  &__block {
    display: none;
    &.active {
      display: block;
    }
  }
  &__inner {
    position: relative;
    display: grid;
    grid-template-columns: repeat(1, minmax(0, 1fr));
    gap: 20px;
    @include screen(laptop) {
      gap: 40px;
      grid-template-columns: repeat(12, minmax(0, 1fr));
    }
    &-full {
      padding-bottom: 100px;
      .contacts__left {
        grid-column: span 12 / span 12;
        display: flex;
        justify-content: space-between;
        align-items: center;
      }
      .contacts__right {
        grid-column: span 12 / span 12;
        table {
          width: 100%;
          tr {
            border-top: 1px solid $black-border;
            border-bottom: 1px solid $black-border;
            margin-top: -1px;
            display: flex;
            flex-direction: column;
            gap: 10px;
            padding: 22px 0;
            @include screen(laptop) {
              padding: 0;
              display: grid;
              grid-template-columns: repeat(12, minmax(0, 1fr));
            }
            td {
              &:nth-child(1) {
                grid-column: span 5 / span 5;
              }
              &:nth-child(2) {
                grid-column: span 7 / span 7;
              }
            }
          }
        }
      }
    }
  }
  &__left {
    @include screen(laptop) {
      grid-column: span 5 / span 5;
    }
    h3 {
      font-family: $stapel;
      font-size: 26px;
      line-height: 31px;
      text-transform: uppercase;
      @include screen(laptop) {
        font-size: 60px;
        line-height: 72px;
      }
      @include screen(desktop) {
        font-size: 80px;
        line-height: 96px;
      }
    }
  }
  &__right {
    @include screen(laptop) {
      grid-column: span 7 / span 7;
    }
    table {
      width: 100%;
      tr {
        border-top: 1px solid $black-border;
        border-bottom: 1px solid $black-border;
        display: flex;
        padding: 22px 0;
        flex-direction: column;
        gap: 10px;
        margin-top: -1px;
        @include screen(laptop) {
          padding: 0;
          display: grid;
          grid-template-columns: repeat(2, minmax(0, 1fr));
        }
        td {
          font-size: 14px;
          line-height: 20px;
          @include screen(laptop) {
            font-size: 16px;
            padding: 20px 0;
            line-height: 20px;
          }
          @include screen(desktop) {
            font-size: 20px;
            padding: 26px 0;
            line-height: 25px;
          }
          &:first-child {
            font-size: 16px;
            line-height: 20px;
            text-transform: uppercase;
            @include screen(desktop) {
              font-size: 20px;
              line-height: 25px;
            }
          }
        }
      }
    }
  }
  &__map {
    margin: 35px 0 50px;
    @include screen(laptop) {
      margin: 70px 0 80px;
    }
    @include screen(desktop) {
      margin: 80px 0 100px;
    }
    img,
    iframe {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__link {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translate(-50%);
    @include screen(laptop) {
      position: static;
      transform: translate(0);
    }
  }
}