.objects {
  padding-bottom: 60px;
  @include screen(laptop) {
    padding-bottom: 80px;
  }
  @include screen(desktop) {
    padding-bottom: 100px;
  }
  &__items {
    margin-top: 45px;
  }
  &__bg {
    position: absolute;
    inset: 0;
    transition: opacity $transition-default;
    opacity: 0;
    img {
      height: 100%;
      width: 100%;
      object-fit: cover;
    }
  }
  &__button {
    margin: 40px auto 0;
    @include screen(laptop) {
      margin: 45px auto 0;
    }
    .button__icon {
      svg {
        max-height: 11px;
        @include screen(laptop) {
          max-height: 19px;
        }
      }
    }
  }
  &__inner {
    position: relative;
    z-index: 2;
  }
  &__item {
    display: block;
    position: relative;
    border-bottom: 1px solid $black-border;
    padding: 40px 0;
    overflow: hidden;
    @include screen(laptop) {
      padding: 50px 0 60px;
      &:before {
        content: '';
        position: absolute;
        bottom: -100%;
        opacity: 0;
        left: 0;
        width: 100%;
        height: 100%;
        transition: opacity 0.5s ease 0s, transform 0.5s ease 0.5s;
        background-color: $blue-primary;
      }
      &:hover {
        &:before {
          opacity: 1;
          transition: opacity 0.5s ease 0s, transform 0.5s ease 0s;
          transform: translateY(-100%);
        }
        .objects__bg {
          opacity: 1;
        }
        .objects__header {
          h2 {
            color: $white-bright;
          }
          p {
            color: $white-primary;
            span {
              color: $white-primary;
              border-color: $white-primary;
            }
          }
        }
        .objects__body {
          p,
          span {
            color: #ADCAE3;
          }
        }
      }
    }
    @include screen(desktop) {
      padding: 70px 0 80px;
    }
    &:first-child {
      border-top: 1px solid $black-border;
    }
  }
  &__header {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    gap: 25px;
    @include screen(laptop) {
      flex-direction: row;
    }
    h2 {
      font-family: $stapel;
      transition: color $transition-default;
      font-size: 20px;
      line-height: 24px;
      @include screen(laptop) {
        font-size: 32px;
        line-height: 38px;
        width: 50%;
      }
      @include screen(desktop) {
        font-size: 45px;
        line-height: 54px;
      }
    }
    p {
      transition: color $transition-default;
      font-weight: 700;
      text-transform: uppercase;
      font-size: 14px;
      color: $black-primary;
      line-height: 19.6px;
      @include screen(laptop) {
        width: 50%;
        text-align: right;
        font-size: 16px;
        line-height: 20px;
      }
      @include screen(desktop) {
        font-size: 20px;
        line-height: 25px;
      }
      span {
        font-weight: 400;
        transition: color $transition-default, border-color $transition-default;
        color: $black-primary;
        display: block;
        margin-top: 15px;
        padding-top: 15px;
        border-top: 1px solid $black-border;
        @include screen(laptop) {
          display: inline;
          margin-left: 12px;
          padding-left: 12px;
          border-top: none;
          margin-top: 0;
          padding-top: 0;
          border-left: 1px solid $black-border;
        }
      }
    }
  }
  &__body {
    display: flex;
    margin-top: 60px;
    justify-content: space-between;
    font-size: 14px;
    line-height: 19.6px;
    color: $black-primary;
    flex-direction: column;
    gap: 25px;
    @include screen(laptop) {
      flex-direction: row;
      margin-top: 80px;
      font-size: 16px;
      line-height: 20px;
    }
    @include screen(desktop) {
      margin-top: 90px;
      font-size: 20px;
      line-height: 25px;
    }
    p {
      @include screen(laptop) {
        max-width: 508px;
        transition: color $transition-default;
      }
    }
    span {
      transition: color $transition-default;
      @include screen(laptop) {
        text-align: right;
        width: 50%;
      }
    }
  }
  &__types {
    .objects__list {
      width: 100%;
      @include screen(tablet) {
        width: 320px;
      }
      @include screen(laptop) {
        width: 407px;
      }
    }
  }
  &__list {
    position: absolute;
    opacity: 0;
    visibility: hidden;
    left: 0;
    padding: 25px;
    top: 30px;
    box-shadow: 0px 0px 5px 0px #0C28401F;
    min-width: 100%;
    display: flex;
    flex-direction: column;
    gap: 25px;
    z-index: 3;
    background-color: $white;
    &.active {
      opacity: 1;
      visibility: visible;
    }
    @include screen(laptop) {
      gap: 28px;
      top: 32px;
    }
    @include screen(desktop) {
      gap: 30px;
    }
  }
  &__element {
    cursor: pointer;
    font-size: 14px;
    line-height: 17px;
    &.active {
      font-weight: 700;
    }
    @include screen(laptop) {
      font-size: 16px;
      line-height: 21px;
    }
    @include screen(desktop) {
      font-size: 20px;
      line-height: 21px;
    }
  }
  &__filters {
    margin-top: 35px;
    display: flex;
    position: relative;
    gap: 16px;
    flex-direction: column;
    @include screen(laptop) {
      position: static;
      flex-direction: row;
      align-items: center;
      margin-top: 60px;
    }
    @include screen(desktop) {
      margin-top: 80px;
    }
    h3 {
      width: 100%;
      display: block;
      color: $black-primary;
      font-size: 14px;
      line-height: 17px;
      @include screen(laptop) {
        font-size: 16px;
        margin-bottom: 0;
        width: auto;
        line-height: 20px;
        display: inline;
      }
      @include screen(desktop) {
        font-size: 20px;
        line-height: 25px;
      }
    }
  }
  &__filter {
    position: relative;
    @include screen(laptop) {
      padding: 0 25px;
      border-right: 1px solid $black-border;
    }
  }
  &__current {
    display: flex;
    align-items: center;
    gap: 6px;
    cursor: pointer;
    &.active {
      svg {
        transform: rotate(180deg);
      }
    }
    @include screen(laptop) {
      gap: 10px;
    }
    span {
      font-size: 14px;
      line-height: 21px;
      @include screen(laptop) {
        font-size: 16px;
        line-height: 21px;
      }
      @include screen(desktop) {
        font-size: 20px;
        line-height: 21px;
      }
    }
    svg {
      width: 8px;
      transition: transform $transition-default;
      @include screen(laptop) {
        width: 14px;
      }
    }
  }
  &__clear {
    position: absolute;
    top: 0;
    right: 0;
    @include screen(laptop) {
      position: static;
      padding-left: 25px;
    }
  }
}