.docs {
  padding: 60px 0;
  @include screen(laptop) {
    padding: 100px 0;
  }
  @include screen(desktop) {
    padding: 120px 0;
  }
  .content {
    .headline {
      margin: 0;
    }
  }
  &__items {
    margin-top: 5px;
    @include screen(laptop) {
      margin-top: 40px;
    }
  }
  &__item {
    padding: 30px 0;
    display: grid;
    gap: 16px;
    grid-template-columns: repeat(12, minmax(0, 1fr));
    border-bottom: 1px solid $black-border;
    @include screen(laptop) {
      align-items: center;
    }
  }
  &__left {
    display: flex;
    grid-column: span 8 / span 8;
    @include screen(laptop) {
      grid-column: span 6 / span 6;
      align-items: center;
    }
  }
  &__title {
    font-size: 14px;
    line-height: 17px;
    margin-left: 50px;
    @include screen(laptop) {
      font-size: 16px;
      line-height: 20px;
      margin-left: 60px;
    }
    @include screen(desktop) {
      margin-left: 70px;
      font-size: 20px;
      line-height: 23px;
    }
  }
  &__right {
    display: flex;
    justify-content: space-between;
    grid-column: span 4 / span 4;
    @include screen(laptop) {
      grid-column: span 6 / span 6;
      align-items: center;
    }
  }
  &__size {
    font-size: 14px;
    line-height: 17px;
    color: #9EA5AB;
    @include screen(laptop) {
      font-size: 16px;
      line-height: 20px;
    }
    @include screen(desktop) {
      font-size: 20px;
      line-height: 23px;
    }
  }
}