.popup {
  position: fixed;
  z-index: 100;
  display: none;
  transform: translate(-50%, -50%);
  left: 50%;
  top: 50%;
  background-color: $blue-primary;
  max-width: 337px;
  width: 100%;
  @include screen(laptop) {
    max-width: 1112px;
  }
  @include screen(desktop) {
    max-width: 1469px;
  }
  &.active {
    display: block;
  }
  &__close {
    position: absolute;
    right: 20px;
    top: 20px;
    z-index: 4;
    @include screen(laptop) {
      display: none;
    }
  }
  &__bg {
    position: fixed;
    inset: 0;
    width: 100%;
    height: 100%;
    z-index: 99;
    display: none;
    &.active {
      display: block;
    }
  }
  &__wrapper {
    @include screen(laptop) {
      display: grid;
      grid-template-columns: repeat(2, minmax(0, 1fr));
    }
  }
  &__image {
    padding: 28px 25px 35px;
    display: flex;
    flex-direction: column;
    gap: 30px;
    position: relative;
    @include screen(laptop) {
      padding: 168px 0 191px 70px;
    }
    @include screen(desktop) {
      padding: 140px 0 160px 100px;
    }
    &:before {
      content: '';
      background-color: rgba($blue-primary, 0.6);
      position: absolute;
      inset: 0;
      width: 100%;
      height: 100%;
      z-index: 2;
    }
    img {
      position: absolute;
      inset: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    .subtitle {
      z-index: 3;
      position: relative;
    }
    p {
      z-index: 3;
      position: relative;
      font-size: 14px;
      line-height: 17px;
      color: $white-bright;
      @include screen(laptop) {
        font-size: 18px;
        line-height: 22px;
      }
      @include screen(desktop) {
        font-size: 24px;
        line-height: 30px;
      }
    }
  }
  &__label {
    width: 100%;
  }
  &__form {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    gap: 20px;
    max-height: calc(100dvh - 190px);
    overflow-y: auto;
    padding: 28px 25px 40px;
    @include screen(laptop) {
      padding: 70px 80px;
    }
    @include screen(desktop) {
      padding: 70px 115px;
    }
  }
  &__input {
    border: 1px solid #83ADD0;
    padding: 12px 20px;
    width: 100%;
    background-color: transparent;
    color: $white-bright;
    @include screen(laptop) {
      padding: 15px 23px;
    }
    &:placeholder {
      color: #8DB4D5;
    }
  }
  &__button {
    width: 100%;
    justify-content: center;
    display: inline-flex;
    @include screen(laptop) {
      justify-content: flex-start;
      width: auto;
    }
    @include screen(laptop) {
      &:hover {
        background-color: $white-bright;
        span {
          color: $blue-primary;
        }
        .button__icon {
          svg {
            path {
              fill: $blue-primary;
            }
          }
        }
      }
    }
  }
  &__policy {
    position: relative;
    display: flex;
    align-items: center;
    cursor: pointer;
    input {
      position: absolute;
      width: 0;
      height: 0;
      opacity: 0;
      visibility: hidden;
      &:checked  {
        & + :after {
          opacity: 1;
        }
      }
    }
    a,
    span {
      font-size: 14px;
      line-height: 17px;
      color: $white-bright;
      @include screen(laptop) {
        font-size: 16px;
        line-height: 20px;
      }
    }
    span {
      display: inline;
      flex-wrap: wrap;
      padding-left: 32px;
      @include screen(laptop) {
        display: flex;
        align-items: center;
      }
      &:before {
        left: 0;
        content: '';
        border: 1px solid $white-bright;
        width: 20px;
        position: absolute;
        height: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
      }
      &:after {
        content: url('/assets/images/check.svg');
        position: absolute;
        left: 4px;
        top: 2px;
        transition: opacity $transition-default;
        opacity: 0;
        @include screen(laptop) {
          top: auto;
        }
      }
    }
    a {
      margin-left: 0;
      border-bottom: 1px solid $white-bright;
      @include screen(laptop) {
        margin-left: 4px;
      }
    }
  }
  &__file {
    border: 1px dashed #83ADD0;
    padding: 14px 20px;
    cursor: pointer;
    display: flex;
    width: 100%;
    align-items: center;
    gap: 16px;
    position: relative;
    svg {
      min-width: 42px;
      min-height: 42px;
    }
    input {
      width: 0;
      height: 0;
      position: absolute;
      opacity: 0;
      visibility: hidden;
    }
    span {
      color: #8DB4D5;
      font-size: 14px;
      line-height: 17px;
    }
  }
}