.history {
  background-color: $blue-primary;
  padding: 45px 0 0;
  @include screen(laptop) {
    padding: 80px 0 0;
  }
  @include screen(desktop) {
    padding: 100px 0 0;
  }
  .content {
    .headline {
      margin: 0;
    }
  }
  &__slider {
    margin-top: 25px;
    position: relative;
    @include screen(laptop) {
      margin-top: 0;
    }
    &__bg {
      height: 100%;
      position: absolute;
      height: 100%;
      width: 100%;
      overflow: hidden;
      inset: 0;
      img {
        position: absolute;
        inset: 0;
        object-fit: cover;
        width: 100%;
      }
    }
    &-sm {
      @include screen(laptop) {
        margin-top: 40px;
      }
      .swiper {
        margin-bottom: -20px;
        padding: 20px 0;
        &-slide {
          text-align: center;
          font-family: $stapel;
          color: #4D88B8;
          transition: transform $transition-default;
          font-size: 20px;
          line-height: 24px;
          position: relative;
          @include screen(laptop) {
            font-size: 30px;
            line-height: 36px;
          }
          @include screen(desktop) {
            font-size: 50px;
            line-height: 60px;
          }
          &-active {
            color: $white-bright;
            transform: scale(1.5);
            &:before {
              content: '';
              position: absolute;
              width: 26px;
              height: 1px;
              background-color: #4D88B8;
              top: 50%;
              transform: translateX(-50%);
              left: 13px;
              @include screen(tablet) {
                left: 40px;
              }
              @include screen(laptop) {
                width: 140px;
                left: 60px;
              }
              @include screen(desktop) {
                width: 188px;
                left: 80px;
              }
            }
            &:after {
              content: '';
              position: absolute;
              width: 26px;
              height: 1px;
              background-color: #4D88B8;
              top: 50%;
              transform: translateX(50%);
              right: 13px;
              @include screen(tablet) {
                right: 40px;
              }
              @include screen(laptop) {
                width: 140px;
                right: 60px;
              }
              @include screen(desktop) {
                width: 188px;
                right: 80px;
              }
            }
          }
        }
        &-button {
          &-prev,
          &-next {
            width: 12px;
            transition: opacity $transition-default;
            @include screen(laptop) {
              width: auto;
            }
            &:before,
            &:after {
              display: none;
            }
          }
        }
      }
    }
    &-lg {
      margin-top: 45px;
      @include screen(laptop) {
        margin-top: 80px;
      }
      .swiper {
        &-slide {
          padding: 90px 40px 110px;
          text-align: center;
          @include screen(laptop) {
            padding: 100px 0 120px;
          }
          &:before {
            content: '';
            position: absolute;
            inset: 0;
            z-index: 2;
            width: 100%;
            height: 100%;
            background-color: $blue-primary;
            opacity: 0.5;
          }
          h3 {
            position: relative;
            z-index: 3;
            font-family: $stapel;
            color: $white-bright;
            font-size: 80px;
            line-height: 96px;
            @include screen(laptop) {
              font-size: 120px;
              line-height: 144px;
            }
            @include screen(desktop) {
              line-height: 192px;
              font-size: 160px;
            }
          }
          p {
            position: relative;
            z-index: 3;
            margin-top: 20px;
            font-family: $stapel;
            color: $white-bright;
            font-size: 18px;
            line-height: 21px;
            @include screen(laptop) {
              font-size: 24px;
              line-height: 28px;
            }
            @include screen(desktop) {
              font-size: 34px;
              line-height: 41px;
            }
          }
        }
      }
    }
  }
}