.services {
  background-color: $blue-primary;
  @include screen(laptop) {
    padding-bottom: 50px;
  }
  &-blue {
    background-color: $blue-dark;
    .services__table {
      background-color: #0D4A82;
      &:before,
      &:after {
        background-color: #0D4A82;
      }
    }
  }
  .headline__subtitle {
    width: 100%;
    display: flex;
    justify-content: space-between;
    br {
      @include screen(tablet) {
        display: none;
      }
    }
    h2 {
      font-family: $stapel;
      color: $white-bright;
      font-size: 22px;
      line-height: 26px;
      @include screen(laptop) {
        font-size: 40px;
        line-height: 48px;
        max-width: 600px;
      }
      @include screen(desktop) {
        font-size: 60px;
        line-height: 72px;
        max-width: 900px;
      }
    }
    h3 {
      font-family: $stapel;
      color: $white-bright;
      font-size: 22px;
      line-height: 26px;
      @include screen(laptop) {
        font-size: 40px;
        line-height: 48px;
      }
      @include screen(desktop) {
        font-size: 60px;
        line-height: 72px;
      }
    }
  }
  &__info {
    position: relative;
    margin-top: 25px;
    padding-top: 25px;
    display: grid;
    grid-template-columns: repeat(1, minmax(0, 1fr));
    @include screen(laptop) {
      margin-top: 30px;
      padding-top: 30px;
      grid-template-columns: repeat(2, minmax(0, 1fr));
    }
    @include screen(desktop) {
      margin-top: 40px;
      padding-top: 40px;
    }
    &:before {
      content: '';
      position: absolute;
      left: 0;
      width: 0;
      background-color: #4D88B8;
      transition: width $transition-default;
      height: 1px;
    }
    &.active {
      &:before {
        width: 100%;
      }
    }
    p {
      font-size: 14px;
      line-height: 19px;
      color: $white-primary;
      margin-top: 20px;
      br {
        display: none;
      }
      &.with-br-pc {
        br {
          @include screen(laptop) {
            display: block;
          }
        }
      }
      &:first-child {
        margin-top: 0;
      }
      @include screen(laptop) {
        font-size: 16px;
        line-height: 20px;
        margin-top: 0;
      }
      @include screen(desktop) {
        font-size: 20px;
        line-height: 25px;
        max-width: 730px;
      }
    }
  }
  .content {
    .subtitle {
      margin: 0;
    }
  }
  &__table {
    background-color: #256CA6;
    padding: 25px 0 30px;
    margin-top: 40px;
    position: relative;
    @include screen(laptop) {
      padding: 40px 0 50px;
    }
    .swiper-slide {
      display: flex;
      align-items: center;
      flex-direction: column;
      @include screen(laptop) {
        align-items: flex-start;
      }
    }
    .swiper-button-prev,
    .swiper-button-next {
      transition: opacity $transition-default;
      width: 6px;
      height: 14px;
      @include screen(laptop) {
        display: none;
      }
      &:before,
      &:after {
        display: none;
      }
    }
    &:before {
      content: '';
      position: absolute;
      left: -9999px;
      background-color: #256CA6;
      height: 100%;
      top: 0;
      width: 9999px;
    }
    &:after {
      content: '';
      position: absolute;
      background-color: #256CA6;
      right: -9999px;
      height: 100%;
      top: 0;
      width: 9999px;
    }
    p {
      position: relative;
      padding: 15px 30px 0;
      margin-top: 30px;
      text-align: center;
      color: $white-bright;
      br {
        display: none;
        @include screen(laptop) {
          display: block;
        }
      }
      @include screen(laptop) {
        padding: 15px 0 0;
        margin-top: 40px;
        text-align: left;
      }
      &:first-child {
        margin-top: 0;
      }
      &:before {
        top: 0;
        content: '';
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        height: 1px;
        width: 0;
        transition: width $transition-default;
        background-color: #4481B4;
        @include screen(laptop) {
          left: 0;
          transform: translateX(0);
          font-size: 16px;
          line-height: 20px;
        }
        @include screen(desktop) {
          font-size: 20px;
          line-height: 25px;
        }
      }
      &.active {
        &:before {
          width: 140px;
          @include screen(laptop) {
            width: 104px;
          }
          @include screen(desktop) {
            width: 140px;
          }
        }
      }
    }
  }
  &__alert {
    display: flex;
    gap: 20px;
    padding-top: 30px;
    padding-bottom: 10px;
    align-items: flex-start;
    @include screen(laptop) {
      align-items: center;
      padding-top: 50px;
      padding-bottom: 20px;
    }
    svg {
      min-width: 27px;
      width: 27px;
      height: 27px;
      min-height: 27px;
      @include screen(laptop) {
        min-width: 42px;
        width: 42px;
        height: 42px;
        min-height: 42px;
      }
    }
    p {
      font-size: 14px;
      line-height: 19px;
      color: #C2D9EC;
      @include screen(laptop) {
        font-size: 16px;
        line-height: 20px;
        max-width: 610px;
      }
      @include screen(desktop) {
        font-size: 20px;
        max-width: 1000px;
        line-height: 25px;
        max-width: 800px;
      }
    }
  }
  &__content {
    display: grid;
    grid-template-columns: repeat(1, minmax(0, 1fr));
    padding: 30px 0;
    gap: 45px;
    font-size: 18px;
    line-height: 27px;
    background-color: #256CA6;
    margin-top: 40px;
    position: relative;
    &:before {
      content: '';
      position: absolute;
      left: -9999px;
      background-color: #256CA6;
      height: 100%;
      top: 0;
      width: 9999px;
    }
    &:after {
      content: '';
      position: absolute;
      background-color: #256CA6;
      right: -9999px;
      height: 100%;
      top: 0;
      width: 9999px;
    }
    @include screen(laptop) {
      margin-top: 50px;
      padding: 50px 0;
      gap: 40px;
      font-size: 24px;
      line-height: 30px;
      grid-template-columns: repeat(2, minmax(0, 1fr));
    }
    @include screen(desktop) {
      font-size: 34px;
      line-height: 43px;
    }
    p.stapel {
      font-family: $stapel;
      color: $white-bright;
    }
  }
  &__about {
    p {
      font-size: 14px;
      line-height: 19px;
      color: $white-bright;
      padding-bottom: 25px;
      margin-bottom: 25px;
      border-bottom: 1px solid #4D88B8;
      @include screen(laptop) {
        font-size: 16px;
        line-height: 20px;
      }
      @include screen(desktop) {
        font-size: 20px;
        line-height: 25px;
      }
    }
    ul {
      list-style-type: disc;
      padding-left: 16px;
      li {
        font-size: 14px;
        line-height: 19px;
        color: $white-bright;
        margin-top: 20px;
        &:first-child {
          margin-top: 0;
        }
        @include screen(laptop) {
          margin-top: 0;
          font-size: 16px;
          line-height: 20px;
        }
        @include screen(desktop) {
          font-size: 20px;
          line-height: 25px;
        }
      }
    }
  }
}