.info {
  padding-bottom: 60px;
  @include screen(laptop) {
    padding: 80px 0;
  }
  @include screen(desktop) {
    padding: 100px 0;
  }
  .content {
    .headline {
      align-items: flex-start;
      margin: 0;
      p {
        font-family: $stapel;
        color: $black-primary;
        margin: 0;
        font-size: 18px;
        line-height: 21px;
        max-width: 303px;
        br {
          @include screen(laptop) {
            display: none;
          }
        }
        @include screen(laptop) {
          font-size: 20px;
          line-height: 30px;
          max-width: 482px;
        }
        @include screen(desktop) {
          font-size: 34px;
          line-height: 43px;
          max-width: 665px;
        }
      }
    }
  }
  &__bg {
    position: absolute;
    inset: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    img {
      width: 100%;
      object-fit: cover;
      height: 100%;
      object-position: top;
      @include screen(laptop) {
        object-position: center;
      }
    }
  }
  &__blocks {
    margin-top: 35px;
    gap: 20px;
    display: grid;
    grid-template-columns: repeat(1, minmax(0, 1fr));
    @include screen(laptop) {
      margin-top: 80px;
      grid-template-columns: repeat(3, minmax(0, 1fr));
    }
    @include screen(desktop) {
      gap: 30px;
      margin-top: 100px;
    }
  }
  &__block {
    display: flex;
    flex-direction: column;
    gap: 20px;
    &:nth-child(2) {
      flex-direction: column-reverse;
      @include screen(laptop) {
        flex-direction: column;
      }
    }
  }
  &__icon {
    left: 0;
    z-index: 2;
    position: absolute;
    left: 30px;
    width: 32px;
    @include screen(laptop) {
      left: 36px;
      top: 22px;
      width: 40px;
    }
    @include screen(desktop) {
      left: 50px;
      top: 30px;
      width: 56px;
    }
    svg {
      width: 100%;
    }
  }
  &__content {
    border: 1px solid $black-border;
    position: relative;
    display: flex;
    align-items: center;
    padding: 23px 30px;
    justify-content: space-between;
    @include screen(laptop) {
      height: 122px;
      align-items: flex-end;
      padding: 22px 36px 30px;
    }
    @include screen(desktop) {
      padding: 25px 35px 40px;
      height: 170px;
    }
    p {
      font-size: 14px;
      margin-left: 82px;
      line-height: 17px;
      @include screen(laptop) {
        font-size: 16px;
        line-height: 20px;
        margin-left: 93px;
      }
      @include screen(desktop) {
        font-size: 20px;
        margin-left: 130px;
        line-height: 25px;
      }
    }
    &-blue {
      border: none;
      @include screen(laptop) {
        padding: 36px 50px;
        height: 243px;
      }
      @include screen(desktop) {
        height: 337px;
        padding: 50px 70px;
      }
      &:before {
        content: '';
        position: absolute;
        inset: 0;
        z-index: 2;
        width: 100%;
        height: 100%;
        background-color: rgba($blue-primary, 0.6);
      }
      p {
        z-index: 2;
        position: relative;
        color: $white-bright;
        @include screen(laptop) {
          margin-left: 0;
        }
      }
      .help__info {
        @include screen(laptop) {
          left: 50px;
          top: 36px
        }
        @include screen(desktop) {
          left: 70px;
          top: 50px;
        }
      }
    }
  }
}