.construction {
  position: relative;
  padding: 0;
  &__bg {
    height: 100%;
    width: 100%;
    position: absolute;
    &:before {
      content: '';
      position: absolute;
      inset: 0;
      width: 100%;
      height: 100%;
      z-index: 2;
      background-color: rgba($blue-primary, 0.6);
    }
    img {
      object-fit: cover;
      object-position: top;
      width: 100%;
      height: 100%;
      @include screen(laptop) {
        height: auto;
      }
    }
  }
  .typography__wrapper {
    padding: 83px 0 45px;
    z-index: 3;
    position: relative;
    .swiper-pagination {
      position: absolute;
      display: flex;
      flex-direction: column;
      gap: 8px;
      z-index: 10;
      align-items: flex-end;
      right: 0;
      left: auto;
      top: 50%;
      bottom: auto;
      width: auto !important;
      transform: translateX(0) translateY(-50%);
      &-bullet {
        left: auto !important;
        background-color: #93B5D2;
        opacity: 1;
        &-active {
          background-color: $white-bright;
        }
      }
    }
    @include screen(laptop) {
      padding: 115px 0 57px;
    }
    @include screen(desktop) {
      padding: 120px 0 70px;
    }
    .breadcrumbs {
      a {
        color: $white-bright;
      }
      span {
        color: #93B5D2;
      }
    }
  }
  .content {
    h1 {
      color: $white-bright;
    }
  }
  &__slider {
    margin-top: 24px;
    .swiper-slide {
      display: flex;
      flex-direction: column-reverse;
      gap: 100px;
      @include screen(laptop) {
        margin-top: 87px;
        display: grid;
        align-items: flex-end;
        grid-template-columns: repeat(2, minmax(0, 1fr));
      }
      @include screen(desktop) {
        margin-top: 95px;
      }
      p {
        color: $white-bright;
        font-size: 14px;
        line-height: 18px;
        @include screen(laptop) {
          font-size: 16px;
          line-height: 20px;
        }
        @include screen(desktop) {
          font-size: 20px;
          line-height: 25px;
        }
      }
      span {
        color: $white-bright;
        font-size: 14px;
        max-width: 267px;
        line-height: 18px;
        @include screen(laptop) {
          font-size: 16px;
          max-width: 555px;
          line-height: 20px;
        }
        @include screen(desktop) {
          font-size: 20px;
          line-height: 25px;
        }
      }
    }
  }
}