.catalog {
  padding-bottom: 0;
  .typography__wrapper {
    .content {
      p {
        br {
          display: none;
          @include screen(laptop) {
            display: block;
          }
        }
      }
    }
  }
  &__items {
    display: grid;
    border-top: 1px solid $black-border;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    margin-top: 45px;
    @include screen(laptop) {
      margin-top: 60px;
      grid-template-columns: repeat(4, minmax(0, 1fr));
    }
  }
  &__item {
    display: flex;
    align-items: center;
    justify-content: center;
    aspect-ratio: 1 / 1;
    border: 1px solid $black-border;
    transition: z-index 0.5s, border-color 0.5s;
    img {
      width: auto;
      height: auto;
      aspect-ratio: 0;
      max-width: 120px;
      max-height: 80px;
      @include screen(laptop) {
        transition: transform $transition-default;
        max-width: 180px;
        max-height: 100px;
      }
      @include screen(desktop) {
        max-width: 100%;
        max-height: 100%;
      }
    }
    @include screen(laptop) {
      &:hover {
        border-color: $black-primary;
        z-index: 2;
        img {
          transform: scale(1.1);
        }
      }
    }
  }
}