.engineering {
  background-color: $blue-primary;
  .content {
    .headline {
      margin: 0;
    }
  }
  &__info {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-top: 25px;
    @include screen(laptop) {
      flex-direction: row;
      margin-top: 50px;
    }
    @include screen(desktop) {
      margin-top: 60px;
    }
  }
  &__item {
    border-top: 1px solid #4D88B8;
  }
  &__body {
    display: none;
    grid-template-columns: repeat(1, minmax(0, 1fr));
    padding: 30px 0;
    @include screen(laptop) {
      grid-template-columns: repeat(2, minmax(0, 1fr));
      gap: 30px;
      padding: 45px 0;
    }
    @include screen(desktop) {
      padding: 50px 0;
    }
    &.active {
      display: grid;
    }
  }
  &__image {
    position: relative;
    img {
      width: 100%;
    }
    width: 100%;
    @include screen(laptop) {
      min-width: 333px;
      max-width: 333px;
    }
    @include screen(desktop) {
      min-width: 444px;
      max-width: 444px;
    }
    &:before {
      content: '';
      position: absolute;
      left: -9999px;
      background-color: #3879AE;
      height: 100%;
      top: 0;
      width: 9999px;
      @include screen(laptop) {
        display: none;
      }
    }
    &:after {
      content: '';
      position: absolute;
      background-color: #3879AE;
      right: -9999px;
      height: 100%;
      top: 0;
      width: 9999px;
      @include screen(laptop) {
        display: none;
      }
    }
  }
  &__text {
    border: 1px solid #4D88B8;
    font-size: 14px;
    padding: 25px 30px;
    line-height: 19px;
    border-top: 0;
    color: $white-bright;
    @include screen(laptop) {
      font-size: 16px;
      border-left: 0;
      border-top: 1px solid #4D88B8;
      line-height: 20px;
      padding: 36px 50px 36px 68px;
    }
    @include screen(desktop) {
      font-size: 20px;
      padding: 52px 110px;
      line-height: 25px;
    }
  }
  &__blocks {
    counter-reset: counter;
    margin-top: 40px;
    display: flex;
    gap: 15px;
    flex-direction: column;
    @include screen(laptop) {
      margin-top: 70px;
      gap: 20px;
    }
    @include screen(desktop) {
      margin-top: 90px;
    }
  }
  &__number {
    position: absolute;
    z-index: 2;
    left: -30px;
    @include screen(laptop) {
      bottom: auto;
      left: 0;
    }
    svg {
      width: 178px;
      @include screen(laptop) {
        width: 156px;
      }
      @include screen(desktop) {
        width: auto;
      }
    }
  }
  &__header {
    counter-increment: counter;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #0D538E;
    gap: 40px;
    padding: 40px 15px 40px 0;
    position: relative;
    cursor: pointer;
    &.active {
      svg {
        transform: rotate(180deg);
        path {
          stroke: $white;
        }
      }
    }
    &:before {
      content: '';
      position: absolute;
      left: -9999px;
      background-color: #0D538E;
      height: 100%;
      top: 0;
      width: 9999px;
    }
    &:after {
      content: '';
      position: absolute;
      background-color: #0D538E;
      right: -9999px;
      height: 100%;
      top: 0;
      width: 9999px;
    }
    @include screen(laptop) {
      padding: 50px 0px 50px 80px;
      gap: 0;
    }
    @include screen(desktop) {
      padding: 57px 0px 57px 108px;
    }
    svg {
      transition: transform $transition-default;
      min-width: 14px;
      max-width: 14px;
      path {
        transition: stroke $transition-default;
      }
      @include screen(laptop) {
        min-width: 29px;
        max-width: 29px;
      }
    }
    h3 {
      z-index: 3;
      font-family: $stapel;
      font-size: 20px;
      text-transform: uppercase;
      color: $white-bright;
      line-height: 24px;
      @include screen(laptop) {
        font-size: 32px;
        line-height: 38px;
      }
      @include screen(desktop) {
        font-size: 45px;
        line-height: 54px;
      }
    }
  }
  &__col {
    &__header {
      padding: 20px 0;
      color: $white-bright;
      @include screen(laptop) {
        font-size: 20px;
        line-height: 25px;
      }
    }
    &__body {
      display: none;
      padding: 25px 0 30px 0;
      @include screen(laptop) {
        padding: 50px 0 45px 0;
      }
      @include screen(desktop) {
        padding: 60px 0 50px 0;
      }
      &.active {
        display: block;
      }
      ul {
        li {
          padding-left: 20px;
          position: relative;
          margin-top: 30px;
          &:before {
            content: '';
            position: absolute;
            left: 0;
            top: 8px;
            width: 7px;
            height: 7px;
            background-color: $white-bright;
            transform: rotate(45deg);
          }
          &:first-child {
            margin-top: 0;
          }
          @include screen(laptop) {
            margin-top: 40px;
            padding-left: 25px;
          }
          p {
            color: $white-primary;
            &:nth-child(2) {
              margin-top: 10px;
            }
          }
          b {
            color: $white-bright;
            font-size: 16px;
            line-height: 20px;
            @include screen(desktop) {
              font-size: 20px;
              line-height: 25px;
            }
          }
        }
      }
    }
  }
}