.brand {
  padding-bottom: 60px;
  @include screen(laptop) {
    padding: 80px 0;
  }
  @include screen(desktop) {
    padding: 100px 0;
  }
  &__block {
    background-color: #D5EBFC;
    position: relative;
    display: flex;
    flex-direction: column-reverse;
    @include screen(laptop) {
      display: grid;
      grid-template-columns: repeat(2, minmax(0, 1fr));
      padding: 60px 90px;
    }
    @include screen(desktop) {
      padding: 70px 120px;
    }
  }
  .typography__wrapper {
    .content {
      .brand__image {
        img {
          margin: -30px auto 0;
        }
      }
    }
  }
  &__inner {
    overflow: hidden;
    padding-top: 45px;
    @include screen(desktop) {
      padding-top: 75px;
    }
  }
  &__content {
    display: flex;
    align-items: flex-start;
    gap: 11px;
    flex-direction: column;
    padding: 26px 24px 32px;
    background-color: #EEF7FF;
    @include screen(laptop) {
      gap: 20px;
      padding: 0;
      background-color: transparent;
    }
  }
  &__subtitle {
    font-family: $stapel;
    color: $black-primary;
    @include screen(laptop) {
      font-size: 24px;
      line-height: 28px;
      color: #3B4A58;
    }
    @include screen(desktop) {
      font-size: 34px;
      line-height: 41px;
    }
  }
  &__text {
    font-size: 14px;
    line-height: 19px;
    color: $black-primary;
    @include screen(laptop) {
      font-size: 16px;
      line-height: 20px;
      color: #3B4A58;
    }
    @include screen(desktop) {
      font-size: 20px;
      line-height: 25px;
    }
    br {
      display: none;
      @include screen(laptop) {
        display: block;
      }
    }
  }
  &__image {
    @include screen(laptop) {
      position: absolute;
      right: 0;
      bottom: 0;
    }
    img {
      max-width: 337px;
      @include screen(laptop) {
        max-width: 629px;
      }
      @include screen(desktop) {
        max-width: 100%;
      }
    }
  }
}